import { Mail, Lock, User, Phone } from "lucide-react";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import toast from "react-hot-toast";
import Button from "./Button";
import Loader from "../Loader";

function setUserIdCookie(userId) {
  const cookieName = "userId";
  const cookieValue = userId;
  const expirationDays = 365;
  const date = new Date();
  date.setTime(date.getTime() + expirationDays * 24 * 60 * 60 * 1000);
  const expires = "expires=" + date.toUTCString();
  document.cookie = `${cookieName}=${cookieValue};${expires};path=/`;
}

const SignupForm = () => {
  const [form, setForm] = useState({
    firstName: "",
    lastName: "",
    username: "",
    email: "",
    phone: "",
    password: "",
    confirmPassword: "",
  });
  const [isValidPassword, setIsValidPassword] = useState(null);
  const [isPasswordMatch, setIsPasswordMatch] = useState(null);
  const [isLoading, setIsLoading] = useState(false);

  const navigate = useNavigate();

  const API_URL =
    window.location.hostname === "localhost"
      ? "http://127.0.0.1:8000/api/user/"
      : "https://allmaxsport.com/api/user/";

  function handleIsValidPassword(value) {
    if (!value) return;
    setIsValidPassword(value.length >= 6);
  }

  function handlePasswordMatch(value) {
    setIsPasswordMatch(value === form.password);
  }

  const validateSignup = () => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    const phoneRegex = /^(09\d{9}|\+98\d{10}|0?9\d{9})$/;

    if (form.firstName.trim().length < 3) {
      toast.error("نام کامل وارد نشده");
      return false;
    }

    if (form.lastName.trim().length < 3) {
      toast.error("نام خوانوادگی کامل وارد نشده");
      return false;
    }

    if (form.username.trim().length < 3) {
      toast.error("نام کاربری کامل وارد نشده");
      return false;
    }

    if (!emailRegex.test(form.email)) {
      toast.error("ایمیل معتبر نیست");
      return false;
    }

    if (!phoneRegex.test(form.phone)) {
      toast.error("شماره موبایل معتبر نیست");
      return false;
    }

    if (form.password.length < 6) {
      toast.error("رمز عبور باید حداقل ۶ کاراکتر باشد");
      return false;
    }

    if (form.password !== form.confirmPassword) {
      toast.error("رمزهای عبور مطابقت ندارند");
      return false;
    }

    return true;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!validateSignup()) return;
    setIsLoading(true);

    try {
      fetch(API_URL, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(form),
      })
        .then((res) => res.json())
        .then((data) => {
          toast.success("ثبت‌نام موفقیت‌آمیز بود!");
          setUserIdCookie(data.id);
          setTimeout(() => navigate("/"), 400);
        });
    } catch (err) {
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-5 text-right">
      <div className="flex flex-row gap-4">
        <div>
          <label className="block text-sm mb-1 text-gray-600">نام</label>
          <div className="relative">
            <input
              type="text"
              placeholder="علی"
              className="w-full border border-gray-200 rounded-xl py-2.5 px-4 pr-10"
              value={form.firstName}
              onChange={(e) => setForm({ ...form, firstName: e.target.value })}
            />
            <User
              className="absolute right-3 top-2.5 text-gray-400"
              size={20}
            />
          </div>
        </div>
        <div>
          <label className="block text-sm mb-1 text-gray-600">
            نام خانوادگی
          </label>
          <div className="relative">
            <input
              type="text"
              placeholder="رضایی"
              className="w-full border border-gray-200 rounded-xl py-2.5 px-4 pr-10"
              value={form.lastName}
              onChange={(e) => setForm({ ...form, lastName: e.target.value })}
            />
            <User
              className="absolute right-3 top-2.5 text-gray-400"
              size={20}
            />
          </div>
        </div>
      </div>

      <div>
        <label className="block text-sm mb-1 text-gray-600">نام کاربری</label>
        <div className="relative">
          <input
            type="text"
            placeholder="نام کاربری"
            className="w-full border border-gray-200 rounded-xl py-2.5 px-4 pr-10"
            value={form.username}
            onChange={(e) => setForm({ ...form, username: e.target.value })}
          />
          <User className="absolute right-3 top-2.5 text-gray-400" size={20} />
        </div>
      </div>

      <div>
        <label className="block text-sm mb-1 text-gray-600">ایمیل</label>
        <div className="relative">
          <input
            type="email"
            placeholder="مثلاً ali@example.com"
            className="w-full border border-gray-200 rounded-xl py-2.5 px-4 pr-10"
            value={form.email}
            onChange={(e) => setForm({ ...form, email: e.target.value })}
          />
          <Mail className="absolute right-3 top-2.5 text-gray-400" size={20} />
        </div>
      </div>

      <div>
        <label className="block text-sm mb-1 text-gray-600">شماره تلفن</label>
        <div className="relative">
          <input
            type="tel"
            placeholder="09123456789"
            className="w-full border border-gray-200 rounded-xl py-2.5 px-4 pr-10"
            value={form.phone}
            onChange={(e) => setForm({ ...form, phone: e.target.value })}
          />
          <Phone className="absolute right-3 top-2.5 text-gray-400" size={20} />
        </div>
      </div>

      <div>
        <label className="block text-sm mb-1 text-gray-600">رمز عبور</label>
        <div className="relative">
          <input
            type="password"
            placeholder="رمز عبور"
            className="w-full border border-gray-200 rounded-xl py-2.5 px-4 pr-10"
            value={form.password}
            onChange={(e) => {
              setForm({ ...form, password: e.target.value.trim() });
              handleIsValidPassword(e.target.value);
              handlePasswordMatch(form.confirmPassword);
            }}
          />
          <Lock className="absolute right-3 top-2.5 text-gray-400" size={20} />
        </div>
        {form.password && (
          <p
            className={`mt-1 text-sm ${
              isValidPassword ? "text-green-500" : "text-red-500"
            }`}
          >
            {isValidPassword
              ? "رمز عبور معتبر است"
              : "رمز عبور باید حداقل ۶ کاراکتر باشد"}
          </p>
        )}
      </div>

      <div>
        <label className="block text-sm mb-1 text-gray-600">
          تأیید رمز عبور
        </label>
        <div className="relative">
          <input
            type="password"
            placeholder="تأیید رمز عبور"
            className="w-full border border-gray-200 rounded-xl py-2.5 px-4 pr-10"
            value={form.confirmPassword}
            onChange={(e) => {
              setForm({ ...form, confirmPassword: e.target.value.trim() });
              handlePasswordMatch(e.target.value);
            }}
          />
          <Lock className="absolute right-3 top-2.5 text-gray-400" size={20} />
        </div>
        {form.confirmPassword && (
          <p
            className={`mt-1 text-sm ${
              isPasswordMatch ? "text-green-500" : "text-red-500"
            }`}
          >
            {isPasswordMatch
              ? "رمزهای عبور مطابقت دارند"
              : "رمزهای عبور مطابقت ندارند"}
          </p>
        )}
      </div>

      {isLoading ? (
        <Button>
          <Loader color="text-white font-bold" center dir="row" />
        </Button>
      ) : (
        <Button>ثبت نام</Button>
      )}
    </form>
  );
};

export default SignupForm;
