import { Mail, Lock, User } from "lucide-react";
import toast from "react-hot-toast";
import { useState } from "react";
import Button from "./Button";
import Loader from "../Loader";
import { useNavigate } from "react-router-dom";
import { Link } from "react-router-dom";

// Save userId in cookie
function setUserIdCookie(userId) {
  const cookieName = "userId";
  const cookieValue = userId;
  const expirationDays = 365; // Cookie will expire in 1 year
  const date = new Date();
  date.setTime(date.getTime() + expirationDays * 24 * 60 * 60 * 1000);
  const expires = "expires=" + date.toUTCString();
  document.cookie = `${cookieName}=${cookieValue}; ${expires}; path=/`;
}

const LoginForm = () => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [rememberMe, setRememberMe] = useState(false);
  const [isLoading, setISLoading] = useState(false);
  const navigate = useNavigate();

  // const API_URL =
  //   window.location.hostname === "localhost"
  //     ? "http://127.0.0.1:8000/api/user/"
  //     : "https://allmaxsport.com/api/user/";

  // Validate username (you can modify validation as needed)
  const validateUsername = (input) => {
    return input.trim().length >= 3; // Example: username >= 3 characters
  };

  function handleRememberMeCheck() {
    setRememberMe((check) => !check);
  }

  const handleSubmit = async (e) => {
    e.preventDefault();
    setISLoading(true);

    if (!validateUsername(username)) {
      toast.error("نام کاربری معتبر نیست");
      setISLoading(false);
      return;
    }

    const loginForm = {
      action: "login",
      username: username,
      password: password,
    };

    try {
      const response = await fetch("https://allmaxsport.com/api/user/", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(loginForm),
      });

      const data = await response.json();

      if (!response.ok || !data?.id) {
        toast.error(data?.message || "ورود ناموفق بود. لطفا دوباره تلاش کنید.");
      } else {
        toast.success("ورود موفقیت‌آمیز بود!");

        if (rememberMe) {
          setUserIdCookie(data.id);
        }

        setTimeout(() => navigate("/"), 500);
      }
    } catch (error) {
      toast.error("خطایی در ورود پیش آمد. لطفا دوباره تلاش کنید.");
    } finally {
      setISLoading(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-5 text-right">
      {/* Username input */}
      <div>
        <label className="block text-sm mb-1 text-gray-600">نام کاربری</label>
        <div className="relative">
          <input
            type="text"
            placeholder="نام کاربری"
            className="w-full border border-gray-200 rounded-xl py-2.5 px-4 pr-10 focus:outline-none focus:ring-2 focus:ring-blue-500"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            required
          />
          <User className="absolute right-3 top-2.5 text-gray-400" />
        </div>
      </div>

      {/* Password input */}  
      <div>
        <label className="block text-sm mb-1 text-gray-600">رمز عبور</label>
        <div className="relative">
          <input
            type="password"
            placeholder="رمز عبور"
            className="w-full border border-gray-200 rounded-xl py-2.5 px-4 pr-10 focus:outline-none focus:ring-2 focus:ring-blue-500"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
          <Lock className="absolute right-3 top-2.5 text-gray-400" size={20} />
        </div>
      </div>

      {/* Remember me & forgot password */}
      <div className="flex justify-between items-center text-sm text-gray-500">
        <label className="flex items-center gap-2 cursor-pointer">
          <input
            type="checkbox"
            checked={rememberMe}
            onChange={handleRememberMeCheck}
            className="form-checkbox rounded border-gray-300"
          />
          <span>مرا به خاطر بسپار</span>
        </label>

        <Link to={"/auth/restpass"}>
          <button type="button" className="text-blue-500 hover:underline">
            فراموشی رمز؟
          </button>
        </Link>
      </div>

      {/* Submit button / loader */}
      {isLoading ? (
        <Button>
          <Loader color="text-white font-bold" center dir="row" />
        </Button>
      ) : (
        <Button>ورود</Button>
      )}
    </form>
  );
};

export default LoginForm;
